import React, { Component } from "react";
import { Loader, ListContainer } from "./general/style/style";
// import { toJS } from "mobx";


export default class Listing extends Component {

  nextPageInProgress = false;

  componentDidMount() {
    if (typeof (this.props.onScrollEnd) !== "undefined") {
      
      const onScrollEndCB = async () => {
        if (this.nextPageInProgress) return;
        this.nextPageInProgress = true;
        this.props.updateLoading(true);
        //console.log("Listing onScrollEnd Called");
        await this.props.onScrollEnd();
        this.props.updateLoading(false);
        this.nextPageInProgress = false;
    }
      typeof(loadFuncOnScrollEnd)==="object" && loadFuncOnScrollEnd.push(onScrollEndCB);
    }
  }

  componentWillUnmount() {
    loadFuncOnScrollEnd.length = 0;
  }

  render() {
    return (
      <ListContainer>
        {React.Children.map(this.props.children, (child) => child)}
        {this.props.loading && <Loader/>}
      </ListContainer>
    );
  }
}


